//CSS ENTRY FILE
@font-face {
    font-family: scoreboard;
    src: url(../fonts/scoreboard.ttf);
}

@font-face {
    font-family: pixel;
    src: url(../fonts/ARCADECLASSIC.TTF);
}


body {
    height: 100vh;
    width: 1000px;
    background: linear-gradient(0deg,
            red 0%,
            yellow 100%);
}


#instructions {
    font-family: pixel;
    font-size: 22px;

}

.canvasContainer {
    display: flex;
    flex-direction: row;
}

.links img {
    height: 75px;
    width: 75px;
}

.links {
    margin-right: 75px;
}

.footer {
    display: flex;
    flex-direction: row;

}



nav {
    display: flex;
    justify-content: space-between;
}

p {
    height: 40px;
    width: 80px;
    background-color: #ececec;
    border: solid 5px black;
    border-radius: 10%;
    padding: 10px;
    font-size: 30px;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

button {
    font-family: 'scoreboard';
    font-weight: bold;
    font-size: 18px;
    height: 50px;
    width: 80px;
    margin-left: 10px;
    margin-right: 10px;
    box-shadow: 10px 5px 5px black;
}

.container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.buttons {
    display: flex;
    flex-direction: row;
    justify-content: center;
}

#title img {
    height: 35px;
    width: auto;
    margin-right: 20px;
}

#homer {
    display: none;
}

#out {
    display: none;
}

#newRecord {
    display: none;
}

#gameOver { 
    display: none;
}

.scores {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    background: transparent;
    height: 300px;
    width: 680px;

}